/*!
*
*   external dependencies
*
!*/
@import '//fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300italic,400italic,500,500italic,700,700italic,900,900italic|Roboto+Condensed:300italic,400italic,700italic,400,300,700|Poiret+One:400|Source+Code+Pro:200,300,400,500,600,700,900';
/*!
*
*   app
*
!*/
/*
*   Bootstrap Overrides
*/
/*
*   Bootstrap Extensions
*/
/*
*   Custom Variables
*/
body {
  padding-bottom: 60px;
}
.navbar.navbar-default {
  border-radius: 0;
  border-top: none;
  border-right: none;
  border-left: none;
}
.navbar-header {
  display: table;
}
.navbar-header .navbar-brand {
  margin: 0;
  padding: 0;
  height: 50px;
  display: table-cell;
  vertical-align: middle;
}
.navbar-header .navbar-brand img {
  margin-top: 7px;
  max-width: 35vw;
}
@media (max-width: 767px) {
  .navbar-header .navbar-brand img {
    margin-left: 2vw;
  }
}
.navbar-nav {
  font-size: 8pt;
}
@media (min-width: 480px) {
  .navbar-nav {
    font-size: 10pt;
  }
}
.navbar-nav li > a {
  line-height: 50px;
  margin: 0;
  padding: 0 3vw;
}
@media (min-width: 480px) {
  .navbar-nav li > a {
    padding: 0 1.5em;
  }
}
.team.red,
.team.red a {
  color: #a94442;
}
.team.blue,
.team.blue a {
  color: #31708f;
}
.team.green,
.team.green a {
  color: #3c763d;
}
.team-bg.red {
  background-color: #F2DEDE;
}
.team-bg.blue {
  background-color: #D9EDF7;
}
.team-bg.green {
  background-color: #DFF0D8;
}
#overview .RegionMatches,
#overview .RegionWorlds {
  padding: 0 1em;
}
#overview .matchContainer + .matchContainer {
  margin-top: .5em;
  padding-top: .5em;
  border-top: 1px solid #eeeeee;
}
#overview .match {
  width: 100%;
  table-layout: fixed;
}
#overview .match tr:hover td {
  font-weight: 700;
}
#overview .match td {
  font-size: 9pt;
  line-height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (min-width: 480px) {
  #overview .match td {
    font-size: 10pt;
  }
}
#overview .match td.score {
  text-align: right;
  width: 30%;
}
#overview .match td.score .diff {
  display: inline-block;
}
#overview .match td.score .value {
  width: 4.5em;
  display: inline-block;
}
#overview .match td.pie {
  width: 70px;
  text-align: right;
}
#overview .match td.pie img {
  width: 60px;
  height: 60px;
}
#tracker .icon-objective-castle,
#tracker .icon-objective-keep {
  zoom: .8;
}
#tracker .icon-objective-tower {
  zoom: .9;
}
#tracker .sprite {
  background-image: resolve('/img/sprite16.png');
  background-repeat: no-repeat;
  display: inline-block;
  width: 16px;
  height: 16px;
}
#tracker .sprite,
#tracker .sprite.neutral,
#tracker .sprite.camp,
#tracker .sprite.neutral.camp {
  background-position: 0px 0px;
}
#tracker .sprite.tower,
#tracker .sprite.neutral.tower {
  background-position: -17px 0px;
}
#tracker .sprite.keep,
#tracker .sprite.neutral.keep {
  background-position: -32px 0px;
}
#tracker .sprite.castle,
#tracker .sprite.neutral.castle {
  background-position: -48px 0px;
}
#tracker .sprite.red,
#tracker .sprite.red.camp {
  background-position: 0px -16px;
}
#tracker .sprite.red.tower {
  background-position: -17px -16px;
}
#tracker .sprite.red.keep {
  background-position: -32px -16px;
}
#tracker .sprite.red.castle {
  background-position: -48px -16px;
}
#tracker .sprite.blue,
#tracker .sprite.blue.camp {
  background-position: 0px -32px;
}
#tracker .sprite.blue.tower {
  background-position: -17px -32px;
}
#tracker .sprite.blue.keep {
  background-position: -32px -32px;
}
#tracker .sprite.blue.castle {
  background-position: -48px -32px;
}
#tracker .sprite.green,
#tracker .sprite.green.camp {
  background-position: 0px -48px;
}
#tracker .sprite.green.tower {
  background-position: -17px -48px;
}
#tracker .sprite.green.keep {
  background-position: -32px -48px;
}
#tracker .sprite.green.castle {
  background-position: -48px -48px;
}
#tracker .objective {
  transition: all 200ms ease-in;
  display: table;
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 12pt;
}
#tracker .objective.fresh {
  background-color: #ffc;
}
#tracker .objective > div {
  display: table-cell;
  vertical-align: middle;
}
#tracker .objective,
#tracker .objective > div {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#tracker .objective .objective-label {
  width: 100%;
}
#tracker .objective .objective-relative span {
  display: inline-block;
  width: 2em;
  text-align: center;
  font-size: 10pt;
}
#tracker .objective .objective-timestamp {
  padding-right: .5em;
  font-size: 10pt;
}
#tracker .objective .objective-map span {
  display: inline-block;
  width: 2.5em;
  text-align: center;
}
@media (min-width: 768px) {
  #tracker .objective {
    font-size: 9pt;
  }
}
@media (min-width: 992px) {
  #tracker .objective {
    font-size: 10pt;
  }
}
@media (min-width: 1200px) {
  #tracker .objective {
    font-size: 11pt;
  }
}
#tracker .objective .sprite {
  margin-right: .2em;
  position: relative;
  top: 2px;
}
#tracker .objective .direction {
  width: 8px;
  height: 8px;
  display: inline-block;
  margin-right: .2em;
}
#tracker .objective .direction img {
  width: 100%;
  height: 100%;
  opacity: .4;
}
#tracker .objective .guildname {
  font-size: 7pt;
  margin-right: 4px;
  cursor: pointer;
}
#tracker .objective .timer.countdown {
  transition: all 800ms ease-in;
  display: inline-block;
  height: 16px;
  color: #333;
  background-color: #ffc;
  line-height: 16px;
  font-size: 8pt;
}
#tracker .objective .timer.countdown.active {
  opacity: 1;
  padding: 0 .5em;
  max-width: 3em;
}
#tracker .objective .timer.countdown.inactive {
  opacity: 0;
  padding: 0;
  max-width: 0;
}
#tracker .objective.highlight .timer.countdown {
  background-color: #f00;
  color: #fff;
}
#tracker #scoreboards .scoreboard {
  margin: 0 1px;
  padding: .5em;
}
#tracker #scoreboards .scoreboard h1 {
  font-size: 18pt;
  font-weight: 400;
  white-space: nowrap;
  padding: 0;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}
#tracker #scoreboards .scoreboard h2 {
  font-size: 14pt;
  font-weight: 400;
  margin: .25em 0 .75em 0;
  padding: 0;
}
#tracker #scoreboards .scoreboard h2 .sub-stats {
  font-size: 10pt;
  padding-top: .25em;
}
#tracker #scoreboards .scoreboard ul {
  margin: 0;
  display: flex;
  justify-content: center;
  align-content: middle;
  flex-flow: row nowrap;
}
#tracker #scoreboards .scoreboard li {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
#tracker #scoreboards .scoreboard li > span {
  display: inline-block;
}
#tracker #scoreboards .scoreboard li > span.quantity {
  font-size: 11pt;
  padding-left: .25em;
}
#tracker #maps {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: space-between;
  margin: 0;
  width: 100%;
}
#tracker #maps h2 {
  margin: 0;
  padding: 0 0 0 1em;
  font-size: 12pt;
  font-weighT: 300;
  border-bottom: 1px solid #eee;
}
@media (min-width: 768px) {
  #tracker #maps {
    flex-direction: row;
  }
}
#tracker #maps .map {
  display: inline-block;
  width: 20em;
  padding: 0 4px;
  margin: 2em auto;
}
@media (min-width: 768px) {
  #tracker #maps .map {
    width: 50%;
  }
}
@media (min-width: 992px) {
  #tracker #maps .map {
    width: 25%;
  }
}
#tracker #maps h1 {
  text-align: center;
}
#tracker #maps .map-section + .map-section {
  padding-top: 4px;
}
#tracker #maps .map-sections {
  display: flex;
  flex-direction: column;
}
#tracker #maps .icon-objective {
  width: 18px;
  height: 18px;
}
#tracker #maps .track-objective {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: 1px 0;
  padding: 0 4px;
  font-size: 9pt;
  line-height: 9pt;
}
#tracker #maps .track-objective:hover {
  font-weight: bold;
  background-color: #ffc;
}
#tracker #maps .track-objective.fresh .track-expire {
  background-color: #ff9;
}
#tracker #maps .track-objective.expiring .track-expire {
  background-color: #f00;
  color: #fff;
}
#tracker #maps .track-objective.expired .track-expire {
  width: 0;
}
#tracker #maps .track-objective img.emblem {
  width: 18px;
  height: 18px;
  position: relative;
}
#tracker #maps .track-objective img.arrow {
  width: 8px;
  height: 8px;
  opacity: .2;
}
#tracker #maps .track-objective > li {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#tracker #maps .track-objective > li.left {
  width: 75%;
}
#tracker #maps .track-objective > li.right {
  width: 25%;
  justify-content: flex-end;
}
#tracker #maps .track-objective > li > * {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
}
#tracker #maps .track-objective .track-sprite {
  width: 24px;
  text-align: center;
}
#tracker #maps .track-objective .track-geo {
  width: 8px;
  text-align: center;
}
#tracker #maps .track-objective .track-expire {
  transition: color 2000ms ease-in, background-color 2000ms ease-in, width 200ms ease-in;
  width: 3em;
  height: 20px;
  line-height: 20px;
  text-align: center;
  margin-left: 4px;
  font-size: 8pt;
}
#tracker #log-container {
  margin: .5em 0;
}
#tracker #log-tabs {
  box-sizing: border-box;
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-end;
  height: 40px;
  border-bottom: 1px solid #ccc;
}
#tracker #log-tabs a.tab {
  display: inline-block;
  padding: 0 1em;
  margin: 0 .1em;
  cursor: pointer;
  border: 1px solid #eee;
  border-bottom: 0;
  border-top-left-radius: .2em;
  border-top-right-radius: .2em;
  font-size: 9pt;
  background-color: #f9f9f9;
  height: 30px;
  line-height: 30px;
}
#tracker #log-tabs a.tab.active {
  background-color: #fff;
  border-color: #ccc;
  font-size: 12pt;
  height: 36px;
  line-height: 36px;
  font-weight: bold;
}
#tracker #log-tabs a.tab:hover {
  border-color: #ccc;
}
#tracker #log-tabs a.check {
  display: flex;
  align-self: flex-end;
  cursor: pointer;
  border: 1px solid #eee;
  border-radius: .2em;
  background-color: #f9f9f9;
  margin: 2px .1em;
  padding: 4px 8px;
}
#tracker #log-tabs a.check.first {
  margin-left: 1em;
}
#tracker #log-tabs a.check.active {
  background-color: #fff;
  border-color: #ccc;
  font-size: 12pt;
  font-weight: bold;
}
#tracker #log-tabs a.check:hover {
  border-color: #ccc;
}
#tracker #log {
  margin: .25em 0;
  height: 25em;
  overflow-y: scroll;
}
#tracker #log > li {
  padding: .1em 0;
  transition: all 150ms ease-in;
}
#tracker #log > li:nth-child(even) {
  background-color: #f9f9f9;
}
#tracker #log > li:hover {
  font-weight: bold;
  background-color: #ffe;
}
#tracker #log .log-objective {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
#tracker #log .log-objective img {
  width: 18px;
  height: 18px;
}
#tracker #log .log-objective img.arrow {
  width: 8px;
  height: 8px;
  opacity: .2;
}
#tracker #log .log-objective > li {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 9pt;
  text-align: left;
}
#tracker #log .log-objective > li.log-sprite {
  width: 30px;
  text-align: center;
}
#tracker #log .log-objective > li.log-time {
  width: 6em;
  text-align: center;
}
#tracker #log .log-objective > li.log-expire {
  width: 3em;
  text-align: center;
}
#tracker #log .log-objective > li.log-name {
  width: 15em;
}
#tracker #log .log-objective > li.log-claimed {
  width: 6em;
}
#tracker #log .log-objective > li.log-guild {
  width: 15em;
}
#tracker #log .log-objective > li.log-geo {
  width: 8px;
  text-align: center;
}
#tracker #options {
  transition: all 400ms ease-in;
  padding-right: 1em;
}
#tracker #options .sub-options {
  padding-left: 1em;
}
#tracker #options .disabled {
  opacity: 0.8;
  blur: .5;
}
#tracker #options h3 .fa-stack {
  font-size: 60%;
}
#tracker #guilds .guild + .guild {
  margin-top: 2em;
}
#tracker #guilds .guild:nth-child(even) {
  background-color: #f6f6f6;
}
#tracker #guilds .guild h1 {
  font-size: 32pt;
  font-size: 24pt;
}
@media (min-width: 992px) {
  #tracker #guilds .guild h1 {
    font-size: 32pt;
  }
}
#tracker #guilds .guild h1 a {
  color: #333333;
}
#tracker #guilds .guild img.emblem {
  width: 256px;
  max-width: 100%;
  height: auto;
}
#tracker #guilds .guild ul {
  margin-left: 1em;
}
#tracker .section-header {
  margin: 1em 0 .5em;
  padding: 0 0 .25em;
  border-bottom: 1px solid #eee;
}
#tracker #AppLoading {
  text-align: center;
  font-size: 10vmax;
  opacity: .5;
}
